* {
    box-sizing: border-box;
    margin: 0;
}
.navbar-container {
    background: #614232;
    width: 100vw;
    height: 50px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 10;
}

.navbar-wrapper {
    display: -webkit-flex;
    display: flex;
    padding: 0 2rem;
    max-width: 1160px;
    height: inherit;
    margin: 0 auto;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
}

.navbar__hamburger {
    display: none;
}

.navbar-wrapper h1 {
    color: #fff;
    font-family: 'Luckiest Guy', cursive;
    font-size: 36px;
    cursor: pointer;
}

.navbar-wrapper ul li {
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
}

.navbar-wrapper ul li:last-of-type {
    border: 1px solid white;
    padding: 5px 24px;
    border-radius: 10px;
}

.navbar-wrapper ul li a {
    color: #fff;
    text-decoration: none;
    position: relative;
}

.order-now:hover {
    background: #3b281f;
}

.navbar__links {
    display: -webkit-flex;
    display: flex;
    list-style-type: none;
    -webkit-align-items: center;
            align-items: center;
}

.navbar__links--link {
    margin: 0 1rem;
}

.navbar__links--link:first-of-type {
    margin-left: 0;
}

.navbar__links--link:last-of-type {
    margin-right: 0;
}

/*Link Animation*/
.navbar-wrapper ul li a::after {
    content: "";
    position: absolute;
    width: 0;
    height: 2px;
    left:0;
    bottom: 0.2px;
    background: #fff;
    transition: all 1s ease;
}

.navbar-wrapper ul li a:hover::after {
    width: 100%;
}

/*Tablet Size*/
@media (min-width: 768px) and (max-width: 990px) {  

}

/*Mobile Landscape Size*/
@media (min-width: 480px) and (max-width: 767px) {  
    .navbar__logo h1{
        font-size: 28px;
    }

    .navbar__links {
        display: none;
    }

    .navbar__hamburger {
        display: block;
        color: #fff;
        font-size: 28px;
        margin-top: 0.5rem;
    }

    .navbar-wrapper {
        -webkit-justify-content: space-between;
                justify-content: space-between;
    }
}

/*Mobile Portrait Size*/
@media (min-width: 240px) and (max-width: 479px) {  
    .navbar__logo h1{
        font-size: 24px;
    }

    .navbar__links {
        display: none;
    }

    .navbar-wrapper {
        -webkit-justify-content: space-between;
                justify-content: space-between;
    }

    .navbar__hamburger {
        display: block;
        color: #fff;
        font-size: 24px;
        margin-top: 0.5rem;
    }
}
.hero-section-container {
